@use "base/variables" as v;
@use "base/mixins" as m;

.directory-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$primaryBlueDark;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.directory-grid {
  margin: 10rem 1rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
  @include m.mobile {
    margin: 7rem 2rem;
  }
  @include m.tablet {
    margin: 5rem 2rem;
    grid-template-columns: 1fr 1fr;
  }
  @include m.desktop {
    grid-template-columns: 1fr;
    margin: 10rem auto;
    gap: 5rem;
    width: 90%;
  }
  .directory-logo {
    background-color: v.$primaryBlueDark;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: v.$white;
    font-family: v.$text;
    text-align: center;
    font-size: 1.25rem;
    padding: 1rem 0;
    @include m.tablet {
      font-size: 1.7rem;
      padding: 1.5rem 0;
    }
    @include m.desktop {
      font-size: 2rem;
      padding: 1.5rem 0;
    }
    @include m.desktopXL {
      font-size: 3rem;
      padding: 1.5rem 0;
    }
    .img-container {
      height: 6rem;
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
      @include m.laptop {
        height: 10rem;
        margin-top: 3rem;
        margin-bottom: 2.5rem;
      }
      img {
        height: 100%;
      }
    }
  }
  .directory-text {
    background-color: v.$grayBackground;
  }
  .directory-odd {
    display: grid;
    grid-template-rows: 1fr 1fr;
    @include m.desktop {
      grid-template-columns: 1fr 4fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        border-radius: 10rem 0 0 0;
      }
    }
    .directory-text {
      border-radius: 0 0 0 0;
      @include m.desktop {
        border-radius: 0 0 10rem 0;
      }
    }
  }
  .directory-even {
    display: grid;
    grid-template-rows: 1fr 1fr;
    @include m.desktop {
      grid-template-columns: 4fr 1fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      order: -1;
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        order: 0;
        border-radius: 0 10rem 0 0;
      }
    }
    .directory-text {
        border-radius: 0 0 0 0;
        @include m.desktop {
            border-radius: 0 0 0 10rem;
        }
    }
  }
}
